<template>
  <div>
    <div>
      <el-card shadow='hover' class='card'>
        <el-divider class='divider' content-position="right" @click='closeNewsDetail'>
          <div class='back'>
            <el-image class='backIcon' :src="require('@/../public/assets/return.png')"></el-image>
            <span class='backWord'>{{$t('News.DetailReturn')}}</span>
          </div>
        </el-divider>

        <div class='content'>
          <div>
            <div class='title'>{{title}}</div>
            <div class='detail'>{{content}}</div>

            <div class='picture'>
              <span v-for='item in picture' v-bind:key='item'>
                <span class='pictureItem'>
                  <el-image :src="item" width="200px"></el-image>
                </span>
              </span>
            </div>
          </div>

          <div class='footer'>
            <div class='auth'>
              <span class='userAuth'>{{userAuth}}</span>
              <span class='school'>{{schoolFormatted}}</span>
            </div>
            <div class='user'>
              <span class='username' @click='openUserPage(userID)'>{{$t('News.DetailAuthor')}}{{username}}
                ({{realname}})</span>
              <span class='link' @click='openLink()'>{{$t('News.DetailLink')}}</span>
            </div>
          </div>
        </div>

        <div class='footer'>
          <div class='action'>
            <span class='time'>{{time}}</span>
            <span class='buttons'>
              <span v-if='!alreadyLike' @click='clickLike' class='like'>
                <el-image class='aAction' :src="require('@/../public/assets/like.png')"></el-image>
                <span>({{like}})</span>
              </span>
              <span v-if='alreadyLike' @click='clickLike' class='like'>
                <el-image class='aAction' :src="require('@/../public/assets/liked.png')"></el-image>
                <span>({{like}})</span>
              </span>
              <el-image class='aAction' :src="require('@/../public/assets/share.png')" @click='clickShare'>
              </el-image>
              <el-image class='aAction' :src="require('@/../public/assets/delete.png')" v-if='isOwner||isAdmin'
                @click='clickDelete'>
              </el-image>
              <!-- <el-image class='aAction' :src="require('@/../public/assets/report.png')" v-if='!isOwner' @click='clickReport'>
              </el-image> -->
            </span>
          </div>

          <div class='comment'>
            <span class='commentBox'>
              <el-input class='inputBox' :placeholder="this.$t('Hollow.DetailCommentTip')" type="textarea" resize='none'
                v-model='inputComment' rows='3'></el-input>
            </span>
            <el-badge class='commentButtons' :value='commentsAmount'>
              <el-button type='primary' @click='submitComment(0)'>{{$t('Hollow.DetailCommentSend')}}</el-button>
            </el-badge>
          </div>
        </div>
      </el-card>

      <div class='comments'>
        <div v-for='item in comments' v-bind:key="item.commentid" class='aComment' :ref='item.commentid'>
          <el-card shadow='hover' class='card comment'>
            <div class='commentTop'>
              <span class='commentUsername' @click='openUserPage(item.userid)'>{{item.username}}</span>
              <span class='commentSchool'>{{item.school}}</span>
              <span class='commentTime'>{{item.time}}</span>
            </div>
            <div class='commentContent'>
              <span class='commentDetail'>{{item.content}}</span>
            </div>
            <div class='commentRight'>
              <el-image class='aAction' :src="require('@/../public/assets/delete.png')" v-if='item.isOwner||isAdmin'
                @click='deleteComment(item.commentid)'>
              </el-image>
              <el-image class='aAction' :src="require('@/../public/assets/report.png')" v-else
                @click='clickReport("nc",item.commentid)'>
              </el-image>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import * as server from '../../../server.js'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'news-detail',
  data () {
    return {
      newsID: 0,
      userID: 0,
      username: '',
      realname: '',
      userAuth: '',
      title: '',
      content: '',
      link: '',
      schoolArray: [],
      isOwner: false,
      alreadyLike: false,
      time: '',
      picture: [],
      comments: [],
      commentsAmount: 0,
      like: 0,
      inputComment: '',
      isAdmin: false,
    }
  },
  computed: {
    schoolFormatted () {
      let schools = '';
      for (let i in this.schoolArray) {
        if (this.schoolArray[i] == 1) {
          schools += this.$t('User.UserSchool' + i) + ' ';
        }
      }
      return schools;
    }
  },
  methods: {
    clickShare () {
      this.copyLink(this.$t('News.DetailShared'));
    },
    closeNewsDetail () {
      this.$bus.$emit('showNewsList')
    },
    getNewsDetail () {
      const loadingInstance = this.loading();
      axios({
        method: 'post',
        url: server.default.getNewsItem,
        data: new URLSearchParams({
          'newsid': this.newsID,
          'cookie': Cookies.get('loginCookie')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'NEWS_GETITEM_COMPLETE') {
            if (this.newsID == response.data.newsid) {
              this.userID = response.data.userid;
              this.username = response.data.username;
              this.realname = response.data.realname;
              this.userAuth = response.data.userauth;
              this.title = response.data.title;
              this.content = response.data.content;
              this.link = response.data.link;
              this.schoolArray = JSON.parse(response.data.school);
              this.isOwner = response.data.isOwner;
              this.alreadyLike = response.data.alreadyLike;
              this.time = this.formatTime(response.data.time);
              if (response.data.picture.length > 0) {
                for (let i in response.data.picture.split(',')) {
                  this.picture.push(server.default.server + response.data.picture.split(',')[i])
                }
              }
              this.comments = response.data.comments;
              this.commentsAmount = this.comments.length;
              this.like = response.data.likes;

              for (let i in this.comments) {
                this.comments[i].time = this.formatTime(this.comments[i].time);
                this.comments[i].school = this.formatSchool(this.comments[i].school);
              }

              setTimeout(() => {
                if (localStorage.getItem('adminworkReportShowOriginalCommentID')) {
                  let target = localStorage.getItem('adminworkReportShowOriginalCommentID');
                  if (this.$refs[target][0]) {
                    this.$refs[target][0].scrollIntoView({ behavior: "smooth", block: "center" });
                    this.$refs[target][0].style = 'background-color: red;border-radius:15px;'
                  }
                  localStorage.removeItem('adminworkReportShowOriginalCommentID')
                }
              }, 1000)
            }
          }
          else {
            this.notfound();
          }
        }
      })
    },
    clickLike () {
      if (this.alreadyLike) {
        this.$message.error(this.$t('News.DetailLiked'));
      }
      else {
        const loadingInstance = this.loading();

        axios({
          method: 'post',
          url: server.default.postNewsLike,
          data: new URLSearchParams({
            'cookie': Cookies.get('loginCookie'),
            'newsid': this.newsID,
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
          }
          else {
            if (response.data == 'NEWS_LIKE_COMPLETE') {
              this.redirect('newsDetail', this.newsID);
            }
          }
        })
      }
    },
    clickDelete () {
      if (this.isOwner || this.isAdmin) {
        this.$confirm(this.$t('News.DetailDeleteWarn'), this.$t('System.Warning'), {
          confirmButtonText: this.$t('System.Yes'),
          cancelButtonText: this.$t('System.No'),
          type: 'warning'
        }).then(() => {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.deleteNewsItem,
            data: new URLSearchParams({
              'cookie': Cookies.get('loginCookie'),
              'newsid': this.newsID,
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else if (response.data == 'NEWS_DELETE_COMPLETE') {
              this.$message.success(this.$t('News.DetailDeleteComplete'));
              this.$bus.$emit('refreshNewsList');
              this.$bus.$emit('showNewsList');
            }
          })
        });
      }
    },
    deleteComment (commentid) {
      for (let i of this.comments) {
        if (commentid == i.commentid || this.isAdmin) {
          this.$confirm(this.$t('News.DetailDeleteWarn'), this.$t('System.Warning'), {
            confirmButtonText: this.$t('System.Yes'),
            cancelButtonText: this.$t('System.No'),
            type: 'warning'
          }).then(() => {
            const loadingInstance = this.loading();

            axios({
              method: 'post',
              url: server.default.deleteNewsComment,
              data: new URLSearchParams({
                'cookie': Cookies.get('loginCookie'),
                'newsid': this.newsID,
                'commentid': commentid
              })
            }).then((response) => {
              loadingInstance.close();
              if (response.status != 200) {
                this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
              }
              else if (response.data == 'NEWS_DELETE_COMPLETE') {
                this.$message.success(this.$t('News.DetailDeleteComplete'));
                this.getNewsDetail();
              }
            })
          });
        }
        break;
      }
    },
    clickReport (target, targetid) {
      this.$prompt(this.$t('General.ReportReason'), this.$t('General.Report'), {}).then((value) => {
        if (!value.value) {
          return;
        }
        else {
          this.report(Cookies.get('loginCookie'), target, targetid, value.value, location.href);
        }
      })
    },
    submitComment () {
      if (this.inputComment.length < 2 || this.title.length > 20) {
        this.alert(this.$t('News.DetailCommentBadLength'), this.$t('System.Warning'));
      }
      else {
        const loadingInstance = this.loading();

        axios({
          method: 'post',
          url: server.default.postNewsComment,
          data: new URLSearchParams({
            'cookie': Cookies.get('loginCookie'),
            'content': this.inputComment,
            'newsid': this.newsID,
            'commentid': nanoid(),
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
          }
          else {
            if (response.data == 'NEWS_COMMENTID_TAKEN') {
              this.submitComment();
              return;
            }
            else if (response.data == 'NEWS_COMMENT_COMPLETE') {
              this.redirect('newsDetail', this.newsID)
            }
          }
        })
      }
    },
    openLink () {
      if (!this.link.includes('http')) {
        if (!this.link.includes('www')) {
          this.link = 'https://www.' + this.link
        }
        else {
          this.link = 'https://' + this.link
        }
      }
      window.open(this.link, "_blank");
    }
  },
  mounted () {
    this.newsID = this.$route.params.id;
    this.getNewsDetail();
    this.isAdmin = this.getIfAdmin();
  }
}
</script>

<style scoped>
.card {
  margin-left: 5%;
  width: 90%;
  border-radius: 20px;
}
.divider {
  width: 90%;
  margin-left: 5%;
}
.back {
  cursor: pointer;
}
.backIcon {
  color: rgb(252, 62, 5);
  width: 20px;
}
.backWord {
  font-size: 18px;
  margin-left: 10px;
}
.content {
  text-align: left;
  margin-left: 10%;
  margin-top: 25px;
  margin-bottom: 40px;
  width: 80%;
}
.title {
  font-size: 35px;
  font-weight: bold;
  margin-bottom: 15px;
}
.detail {
  font-size: 25px;
}
.picture {
  margin-top: 20px;
  margin-bottom: 30px;
}
.picture .pictureItem {
  margin-right: 15px;
}
.footer {
  margin-top: 40px;
}
.auth {
  margin-bottom: 15px;
}
.userAuth {
  margin-right: 25px;
  font-weight: bold;
}
.school {
  margin-right: 25px;
}
.username {
  cursor: pointer;
}
.link {
  float: right;
  text-decoration: underline;
  cursor: pointer;
}
.footer {
  margin-bottom: 30px;
}
.action {
  width: 80%;
  margin-left: 10%;
  display: flex;
  justify-content: space-between;
}
.like span {
  position: relative;
  top: -6px;
  left: -15px;
}
.time {
  text-align: left;
}
.buttons {
  text-align: right;
  font-size: 20px;
  margin-bottom: 20px;
}
.aAction {
  width: 25px;
  margin-right: 15px;
  cursor: pointer;
}
.inputBox {
  width: 55%;
  margin-right: 25px;
  font-size: 18px;
  border-radius: 10px;
}
.inputBox ::v-deep textarea {
  border-radius: 15px;
}
.commentButtons .el-button {
  font-size: 15px;
  border-radius: 15px;
}
.commentButtons {
  position: relative;
  top: -25px;
}
.comment {
  margin-bottom: 10px;
}
.aComment {
  text-align: left;
  width: 90%;
  margin-left: 5%;
  margin-top: 30px;
}
.commentUsername {
  font-size: 25px;
  font-weight: bold;
  cursor: pointer;
}
.commentSchool {
  margin-left: 20px;
  margin-right: 20px;
}
.commentContent {
  margin-top: 15px;
  width: 100%;
}
.commentDetail {
  width: 80%;
}
.commentRight {
  float: right;
  margin-right: 5%;
  margin-bottom: 15px;
}
</style>